/*
 * Play video if specific url hash exists
 */
(function() {
	"use strict";

	/*
	 * handle hash change
	 */
	function handleHashChange () {

		var hash;

		// get hash value
		if (window.location.hash) {

			hash = window.location.hash.substring(1);
			if (hash === "vmdr-video") {
				var poster = document.querySelector(".poster");
				var playIcon = document.querySelector(".play-icon");
				var playOverlay = document.querySelector(".q-video-poster-overlay");
				var iframe = document.querySelector("iframe");
				var player = new Vimeo.Player(iframe);
				poster.classList.add("hidden");
				playIcon.classList.add("hidden");
				playOverlay.classList.add("hidden");
				player.play();
			}

		}
	}

	window.addEventListener("hashchange", handleHashChange);
	document.addEventListener("DOMContentLoaded", handleHashChange);
} ());



(function ($) {

	"use strict";

	// expand / collapse table

	$(".expand-accordion").on("click", function (element) {
		var $buttonSpan = $(element.currentTarget).find("span");
		var buttonClass = $buttonSpan.attr("class");
		var header = $(".vmdr-workflow-table2 .header");
		var section = header.next(".section");

		if (buttonClass === "expand") {
			$buttonSpan.attr("class", "");
			$(element.currentTarget).find("span").text("Expand All");
			header.removeClass("expanded2");
			section.slideUp("fast");
		} else {
			$buttonSpan.attr("class", "expand");
			$(element.currentTarget).find("span").text("Collapse All");
			header.addClass("expanded2");
			section.slideDown("fast");
		}
	});

	$(".vmdr-workflow-table2 .header").each( function(){
		var $header =  $(this);
		var $section =  $header.next(".section");
		$header.on("click", function() {
			$header.toggleClass("expanded2");
			$section.slideToggle();
		});
	});

	// video overlay hide on play

	$(".play-icon, .video-link").on("click", function(){
		$(".video-overlay").addClass("hidden");
	});

	// video playlist

	$(".video-link").on("click", function(){
		var ths = $(this);
		var videoText = ths.text();
		$(".active-video").text( videoText ).removeClass("menu-active");
		$(".video-links-list").fadeOut( "fast" );
	});

	$(".active-video").on("click", function(){
		$(".video-links-list").fadeToggle( "fast" );
		$(this).toggleClass("menu-active");
	});

	$(document).mouseup(function(e) {
		var container = $(".video-playlist");

		if (!container.is(e.target) && container.has(e.target).length === 0){
			$(".video-links-list").hide();
			$(".active-video").removeClass("menu-active");
		}
	});

}(jQuery));

